import { Component, Host, Prop, State, h } from '@stencil/core';
import { Memoize, isEmpty, parseIfString } from '@springbokagency/stencil-base';
import { CheckboxItem } from './form-checkbox.types';

/**
 * The form checkbox component.
 */
@Component({
  tag: 'bs-form-checkbox',
  styleUrl: 'form-checkbox.scss',
  shadow: true,
})
export class BsFormCheckbox {
  private formFieldRef: HTMLBsFormFieldElement;

  /**
   * State
   */

  @State() invalid: boolean = false;

  @State() focused: boolean = false;

  @State() value: string[] = [];

  /**
   * The unique name to identify the form element
   */
  @Prop() name!: string;

  /**
   * The error message to be displayed when the required field remains empty
   */
  @Prop() requiredMessage: string;

  /**
   * Wether or not the checkbox is required
   */
  @Prop() required?: boolean = true;

  /**
   * The label displayed after the checkbox
   */
  @Prop() label?: string = '';

  /**
   * Description.
   */
  @Prop() options: CheckboxItem[] | string;

  @Memoize('options')
  get parsedOptions(): CheckboxItem[] {
    return parseIfString(this.options);
  }

  /**
   * Methods
   */

  private handleOnChange = (e: PointerEvent) => {
    const el = e.target as HTMLInputElement;

    if (el.checked) {
      this.value.push(el.value);
      this.value = [...this.value];
    } else {
      this.value = this.value.filter((item) => item !== el.value);
    }

    this.formFieldRef.value = this.value.toString();
  };

  componentDidLoad() {
    const selected = [] as string[];

    this.parsedOptions.forEach((option) => {
      if (option.selected) {
        selected.push(option.value);
      }
    });

    this.value = selected;
    this.formFieldRef.value = this.value.toString();
  }

  render() {
    return (
      <Host>
        <fieldset class="form-fieldset">
          <legend class="form-legend">
            {this.label}
            {this.required && !isEmpty(this.label) ? <span aria-hidden="true">&nbsp;*</span> : ''}
          </legend>
          <bs-form-field
            ref={(el) => {
              this.formFieldRef = el;
            }}
            value={this.value.toString()}
            name={this.name}
            invalid={this.invalid}
            required={this.required}
            requiredMessage={this.requiredMessage}
            label={this.label}
            border={false}
          >
            {this.parsedOptions.map((checkbox, index) => (
              <div slot="form-element" class="form-group">
                <input
                  class="form-input"
                  id={`checkbox-${index}`}
                  type="checkbox"
                  value={checkbox.value || 'true'}
                  disabled={checkbox.disabled}
                  checked={checkbox.selected}
                  onChange={this.handleOnChange}
                />
                <label class="form-label" htmlFor={`checkbox-${index}`} title={checkbox.text}>
                  {checkbox.text}
                </label>
              </div>
            ))}
          </bs-form-field>
        </fieldset>
      </Host>
    );
  }
}
