:host {
  @include reset-host;
}

:host .form-fieldset {
  border: 0;
}

:host .form-legend {
  margin-bottom: 0.5rem;
}

:host .form-group {
  display: flex;
  margin-bottom: 0.5rem;
}

:host .form-input {
  width: rem(22);
  height: rem(22);
  padding: 1px;
  border: 1px solid $gray-500;
  border-radius: 4px;

  + .form-label {
    @include font(label);
    top: 0;
    margin-right: rem(10);
    margin-left: rem(10);
    line-height: rem(22);
  }

  &:focus {
    outline: 1px solid $gray-400;
  }
}
